import React, {Component} from 'react';

export default class AboutUs extends Component {

  render() {
    return(
      <div className="section" id="om-oss">
        <div className="sectionContent">
          <h1>Hvem er vi?</h1>
          <p>
          Output er bandet tilhrende linjeforeningen Online, linjeforeningen for informatikk ved NTNU i Trondheim.
          Bandet har eksistert siden hsten 2017, og har i skrivende stund 8 medlemmer.
          <br/><br/>
          Bandet opptrer regelmessig p Online sine arrangementer og sporadisk andre steder.
          Vi holder til p NTNU Glshaugen, og disponerer ogs vre vingslokaler her. Vi spiller mye variert musikk, og beveger oss fort innom mange forskjellige sjangere i lpet av en opptreden.
          <br/><br/>
          Har du noen sprsml, spiller du et instrument eller har du behov for god stemning p ditt arrangement? Kontakt oss gjerne p mail, band@online.ntnu.no
          </p>
        </div>
      </div>
    )
  }
}



